import React from 'react';
import ReactDOM from 'react-dom';
import {
  BrowserRouter,
  Switch,
  Route,
} from 'react-router-dom';
import Client from "./components/client"
import LoginForm from "./components/LoginForm"

class App extends React.Component {
  constructor() {
    super();
    this.user = null;
  }

  getUser = async ({ email, password }) => {
    return fetch('/api/login',{
      method: 'POST',
      body: JSON.stringify({ email, password }),
    })
    .then((res) => JSON.parse(res.body))
    .catch((err) => {
      console.error(err.message);
      return null;
    });
  };

  onLoginHandler = async (data) => {
    const user = await this.getUser(data);
    if (!user) return;
    this.user = user;
    this.render();
  };

  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route exact path="/admin">
            { !this.user && <LoginForm onSubmit={this.onLoginHandler}/> }
            { this.user && <Client user={this.user} /> }
          </Route>
          <Route path="/" component={Client} />
          {/* <Route path="*" component={Page404} /> */}
        </Switch>
      </BrowserRouter>
    );
  }

  // get_content() {
  //   let content;
  //   if (this.state.username === 'Nobody')
  //     content = <Login
  //         login={this.login}
  //         message = {this.state.message}
  //     />;
  //   else if (this.state.username === 'admin') {
  //     content = <Admin
  //         brokers={this.state.brokers}
  //         start_torgs={this.start_torgs}
  //     />;
  //   }
  //   else
  //     content = <User
  //         brokers={this.state.brokers}
  //         username ={this.state.username}
  //         stocks ={this.state.stocks}
  //         sell ={this.sell}
  //         buy = {this.buy}
  //     />;
  //   return content;
  // }

}
ReactDOM.render(<App />, document.getElementById("react-root"));
export default App;

